<template>
   <div class="bill-card-search-header content-box">
     <div class="search">
       <div class="search-type">
         <el-select v-model="queryParams.searchType" size="small" placeholder="">
           <template #prefix>
             <span>:</span>
           </template>
           <el-option
              v-for="item in ['','','','','','/']"
              :key="item"
              :value="item"
              :label="item"
           />
         </el-select>
       </div>
       <el-input v-model="queryParams.searchKey" placeholder="" class="keyword" @keyup.enter="handleQuery"/>
       <div class="blue" @click="handleQuery"></div>
       <div class="blue reset" @click="resetQuery"></div>
       <div class="blue toggle" @click="toggleMore = !toggleMore">
         {{ toggleMore ? '' : '' }}
       </div>
     </div>

     <!--  -->
     <div class="quick-keywords">
       
       <el-link @click="searchShow('')"></el-link>
       |
       <el-link @click="searchShow('')"></el-link>
       |
       <el-link @click="searchShow('')"></el-link>
       |
       <el-link @click="searchShow('')"></el-link>
       |
       <el-link @click="searchShow('')"></el-link>
       |
       <el-link @click="searchShow('')"></el-link>
       |
       <el-link @click="searchShow('')"></el-link>
       |
       <el-link @click="searchShow('')"></el-link>
       |
       <el-link @click="searchShow('')"></el-link>
       |
       <el-link @click="searchShow('')"></el-link>
       |
       <el-link @click="searchShow('')"></el-link>
       |
       <el-link @click="searchShow('')"></el-link>
       |
       <el-link @click="searchShow('')"></el-link>
     </div>

     <!--  -->
     <div class="more" v-if="toggleMore">
       <div class="search-row">
         
         <el-radio-group v-model="queryParams.flowType">
           <el-radio-button label=""></el-radio-button>
           <el-radio-button label="WECHAT"></el-radio-button>
           <el-radio-button label="ALIPAY"></el-radio-button>
         </el-radio-group>
         &nbsp;
         <el-radio-group v-model="queryParams.txType">
           <el-radio-button label=""></el-radio-button>
           <el-radio-button label=""></el-radio-button>
           <el-radio-button label=""></el-radio-button>
           <el-radio-button label=""></el-radio-button>
         </el-radio-group>
       </div>
       <div class="search-row">
         
         <el-button-group>
           <el-button @click="chooseDate(1)">1</el-button>
           <el-button @click="chooseDate(2)">2</el-button>
           <el-button @click="chooseDate(3)">3</el-button>
           <el-button @click="chooseDate(12)">1</el-button>
         </el-button-group>
         &nbsp;
         <el-date-picker
           v-model="queryParams.dateRange"
           type="daterange"
           format="YYYY-MM-DD"
           value-format="YYYY-MM-DD"
           range-separator=""
           start-placeholder=""
           end-placeholder=""
           style="max-width: 300px;"
         ></el-date-picker>
       </div>
       <div class="search-row">
         
         <el-button-group>
           <el-button @click="setAmountRange(0, 500)">0 - 500</el-button>
           <el-button @click="setAmountRange(500, 1000)">500 - 1000</el-button>
           <el-button @click="setAmountRange(1000, 2000)">1000 - 2000</el-button>
           <el-button @click="setAmountRange(2000, 3500)">2000 - 3500</el-button>
           <el-button @click="setAmountRange(3500, null)">3500+</el-button>
         </el-button-group>
         &nbsp;
         <el-input v-model="queryParams.amountRange1" placeholder="" clearable
                   @keyup.enter="handleQuery" style="width:110px;"/> -
         <el-input v-model="queryParams.amountRange2" placeholder="" clearable
                   @keyup.enter="handleQuery" style="width:110px;"/>
       </div>
     </div>
   </div>
</template>

<script>
export default {
  name: "BillCardSearchHeader",
  data() {
    return {
      toggleMore: false,
      queryParams: {
        searchType: '',
        searchKey: '',
        flowType: '',
        txType: '',
        dateRange: [],
        amountRange1: '',
        amountRange2: ''
      }
    }
  },
  methods: {
    handleQuery() {
      this.$emit('search', this.queryParams);
    },
    resetQuery() {
      this.queryParams = {
        searchType: '',
        searchKey: '',
        flowType: '',
        txType: '',
        dateRange: [],
        amountRange1: '',
        amountRange2: ''
      };
      this.$emit('search', this.queryParams);
    },
    searchShow(keyword) {
      this.queryParams.searchKey = keyword;
      this.handleQuery();
    },
    chooseDate(months) {
      const endDate = new Date();
      const startDate = new Date();
      startDate.setMonth(startDate.getMonth() - months);
      
      this.queryParams.dateRange = [
        startDate.toISOString().split('T')[0],
        endDate.toISOString().split('T')[0]
      ];
    },
    setAmountRange(min, max) {
      this.queryParams.amountRange1 = min;
      this.queryParams.amountRange2 = max || '';
    },
    updateSearchParams(params) {
      // 
      this.queryParams = { ...params };
    }
  }
}
</script>

<style scoped lang="less">
.content-box {
  font-size: .07813rem;
  .search {
    height: .26042rem;
    display: flex;
    align-items: center;
    margin-bottom: .05208rem;
    position: relative;
    .search-type {
      width: 1.04167rem;
      margin-right: .0625rem;
      color: #a8abb2;
    }
    .el-select,.el-select__wrapper.el-tooltip__trigger.el-tooltip__trigger{
      border: none !important;
    }
    :deep(.el-select__wrapper) {
      align-items: center;
      background-color: var(--el-fill-color-blank);
      border-radius: var(--el-border-radius-base);
      box-shadow: 0 0 0 .00521rem var(--el-border-color) inset;
      box-sizing: border-box;
      cursor: pointer;
      display: flex;
      font-size: .07292rem;
      gap: .03125rem;
      line-height: .125rem;
      min-height: .16667rem;
      padding: .02083rem .0625rem;
      position: relative;
      text-align: left;
      transition: var(--el-transition-duration);
    }
    .keyword {
      width: 1.04167rem;
      height: .16667rem;
      border: none;
      background: #f7f7f7;
      border-radius: .02604rem;
      color: #b6b6b6;
    }
    .blue {
      display: flex;
      height: .16667rem;
      padding: 0 .08333rem;
      justify-content: center;
      align-items: center;
      color: #fff;
      font-size: .07292rem;
      font-weight: 400;
      border-radius: .02083rem;
      background: #418dff;
      margin: 0 .0625rem;
      cursor: pointer;
    }
    .reset {
      color: #333;
      background: #fff;
      border: .00521rem solid #ddd;
      margin-left: 0;
    }
    .toggle {
      margin-left: 0;
    }
  }

  .quick-keywords {
    margin-bottom: .05208rem;
    font-size: .07292rem;
    color: #666;
    
    .el-link {
      margin: 0 .02083rem;
      font-size: .07292rem;
    }
  }

  .more {
    background: #f9f9f9;
    padding: .05208rem;
    border-radius: .02604rem;
    
    .search-row {
      display: flex;
      align-items: center;
      margin-bottom: .05208rem;
      font-size: .07292rem;
      
      &:last-child {
        margin-bottom: 0;
      }
      
      .el-radio-group {
        margin-right: .0625rem;
      }
      
      .el-button-group {
        margin-right: .0625rem;
        
        .el-button {
          font-size: .07292rem;
          padding: .02083rem .04167rem;
        }
      }
      
      .el-date-picker {
        width: 1.5rem;
      }
      
      .el-input {
        font-size: .07292rem;
      }
    }
  }
}
</style>
