<template>
   <div class="card-box" style="margin-bottom: 0.10417rem;">
     <div class="card-header">
       <img alt="" src="@/assets/images/arrow-right.png"/>
       -TOP
       <el-tooltip placement="right" popper-class="monitor-yt-popover" :visible-arrow="false">
         <i class="iconfont icon-question"/>
         <template v-slot:content>
           <div class="bot__pop"
                style="padding: 0;width: 2.60417rem;line-height: .125rem;font-size: .0625rem;">
             
           </div>
         </template>
       </el-tooltip>
     </div>
     <div class="card-opera-group">
       <change-tab-group :tab-data="['TOP','TOP']" primary @change="handleTabChange"/>
     </div>
     <div class="sub-group">
       <tag-group :tags="['7','15','1','3','','1']" @change="handleTimeRangeSelect"/>
     </div>
     <div class="card-content" style="margin-top: 0.10417rem;">
       <el-table
           ref="tableRef"
           :data="tableData"
           height="420"
           :header-cell-style="{
                    textAlign: 'center',
                    backgroundColor: '#F5F7FA',
                  }"
           :cell-style="{ textAlign: 'center' }"
           :default-sort="defaultSort"
       >
         <el-table-column
             type="index"
             label=""
             width="60"
         />
         <el-table-column
             prop="counterpart"
             label=""
         />
         <el-table-column
             prop="count"
             label=""
             sortable
         />
         <el-table-column
             prop="totalAmount"
             label=""
             sortable
         />
         <el-table-column
             prop="totalOut"
             label=""
         />
         <el-table-column
             prop="totalIn"
             label=""
         />
         <el-table-column
             prop="totalOther"
             label=""
         />
         <el-table-column
             prop="totalBalance"
             label=""
         />
         <el-table-column
             label=""
         >
           <template #default="scope">
             <!--  -->
             <el-button type="text" size="small" @click.stop="handleOpenLocation(scope.row)"></el-button>
           </template>
         </el-table-column>
       </el-table>
     </div>
   </div>
</template>

<script>
import ChangeTabGroup from "@/components/ChangeTabGroup";
import TagGroup from "@/components/TagGroup";
export default {
  name: "FlowOppositePartyCard",
  components: {TagGroup, ChangeTabGroup},
  emits: ['openLocation'],
  props: {
    data: {
      type: Object,
      default: () => ({})
    }
  },
  data() {
    return {
      currentTimeRange: '7',
      currentTab: 0, // 0: TOP, 1: TOP
    }
  },
  computed: {
    tableData() {
      // 
      const timeRangeMap = {
        '7': 'counterpartTop7',
        '15': 'counterpartTop15',
        '1': 'counterpartTop30',
        '3': 'counterpartTop90',
        '': 'counterpartTop180',
        '1': 'counterpartTop360'
      }
      return this.data[timeRangeMap[this.currentTimeRange]] || [];
    },
    defaultSort() {
      // TOPcountTOPtotalAmount
      if (this.currentTab === 0) {
        return { prop: 'count', order: 'descending' }
      } else if (this.currentTab === 1) {
        return { prop: 'totalAmount', order: 'descending' }
      }
      return {};
    }
  },
  methods: {
    handleTimeRangeSelect(index) {
      const timeRanges = ['7','15','1','3','','1'];
      this.currentTimeRange = timeRanges[index];
      console.log(this.currentTimeRange)
    },
    handleTabChange(index) {
      this.currentTab = index;
      // TOPcountTOPtotalAmount
      if (index === 0) {
        this.$refs.tableRef.sort('count', 'descending');
      } else if (index === 1) {
        this.$refs.tableRef.sort('totalAmount', 'descending');
      }
    },
    handleOpenLocation(row) {
      this.$emit('openLocation', row)
    }
  }
}
</script>

<style scoped>

</style>
